<template>
	<view class="cartContainer">
		<view class="title">购物车</view>
		<view class="header">
			<text>30天无忧退货</text>
			<text>48小时快速退货</text>
			<text>满99元免邮费</text>
		</view>
		
		<view class="contentContainer" v-if="!checkLogin">
			<image class="cartImg" src="http://yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/icon-normal/noCart-d6193bd6e4.png?imageView&type=webp" mode=""></image>
			<button @click="toLogin">登录</button>
			<view class="addMore">去添加点什么吧</view>
		</view>
		
				<!-- 购物车列表 -->
		<view class="cartList" v-if="shopCartList.length && checkLogin">
			<view class="cartItem" v-for="item in shopCartList" :key="item.id">
				<text class='iconfont icon-xuanzhong' :class="{selected:item.selected}" @click="changeSelected(item)"></text>
				<view class="shopItem">
					<image class="shopImg" :src="item.listPicUrl" mode=""></image>
					<view class="shopInfo">
						<text>{{item.name}}</text>
						<text class="price">￥ {{item.retailPrice}}</text>
					</view>
				</view>
				<!-- 控制数量 -->
				<view class="countCtrl">
					<text class="add" @click="addOrReduceCartCount(item,true)"> + </text>
					<text class="count"> {{item.count}} </text>
					<text class="del" @click="addOrReduceCartCount(item,false)"> - </text>
				</view>
			</view>
		</view>
		<!-- 底部下单 -->
		<view class="cartFooter">
			<text class='iconfont icon-xuanzhong' :class="{selected:allSelected}" @click="changeAllSelected"></text>
			<text class="allSelected">已选 {{selectedcuont}}</text>
			<view class="right">
				<text class="totalPrice">合计: ￥{{totalPrice}}</text>
				<text class="preOrder" @click="placeOrder">下单</text>
			</view>
		</view>
				
			<view v-if="checkLogin && !shopCartList.length">
				<image class="cartImg" src="http://yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/icon-normal/noCart-d6193bd6e4.png?imageView&type=webp" mode=""></image>
						<view class="hint">购物车还是空的，赶紧去购物吧</view>
			</view>
	
	</view>
</template>

<script>
	export default {
		data() {
			return {
				shopCartList:[]
			}
		},
		mounted() {
			this.getShopCartList();
		},
		methods:{
			async getShopCartList() {
				await this.$http("/getShopCartList").then(res => {
					this.shopCartList = res;
				});
			},
			addOrReduceCartCount(item,flag) {
				if(flag) {
					item.count += 1;
				}else {
					if(item.count <=1) {
						return;
					}else {
						item.count -=1;
					}
				}
			},
			changeSelected(item) {
				item.selected = !item.selected;
			},
			placeOrder() {
				console.log("xxxx");
			},
			changeAllSelected() {
				let flag = this.allSelected;
				if(flag) {
					this.shopCartList.forEach(item => item.selected = !flag);
				}else {
					this.shopCartList.forEach(item => item.selected = !flag);
				}
			}
		},
		computed:{
			checkLogin() {
				let userInfo = wx.getStorageSync("userInfo");
				return userInfo.nickName ? true : false
			},
			selectedcuont() {
				return this.shopCartList.reduce((pre,next)=>{
					if(next.selected) {
						pre+=1;
					}
					return pre
				},0)
			}
			,
			totalPrice() {
				return this.shopCartList.reduce((pre,next)=>{
					if(next.selected){
						pre+= next.count * next.retailPrice;
					}
					return pre
				},0)
			},
			allSelected:{
				get:function() {
					return this.shopCartList.every(item => (item.selected));
				},
			}
		}
	}
</script>

<style lang="stylus">
	/* iconfont 公共样式 */
	.cartImg
		display block
		width 248upx
		height 248upx
		margin 300upx auto 50upx

	.cartContainer
		position relative
		background #f4f4f4
		height 100%
		.title
			font-size 32upx
			line-height 80upx
			margin-left 30upx
		.header
			display flex
			background #eee
			text
				width 33.333%
				height 80upx
				line-height 80upx
				text-align center
				font-size 26upx
		.contentContainer
			
			button
				width 480upx
				height 92upx
				background #DD1A21
				color #fff
				font-size 32upx
				line-height 92upx
			.addMore
				text-align center
				font-size 26upx
				color #7f7f7f
				line-height 60rpx
		.cartList
			margin-bottom 96rpx
			.cartItem
				position relative
				display flex
				height 172upx
				background #fff
				margin-top 20upx
				padding 20upx
				.iconfont
					font-size 40upx
					line-height 172upx
					margin 0 40upx
					color #999
					&.selected
						color: #BB2C08
				.shopItem
					display flex 
					.shopImg
						width 172upx
						height 172upx
						background #eee
					.shopInfo
						font-size 28upx
						display flex
						flex-direction column
						margin-left 20upx
						text
							line-height 60upx
						.price
							color: #BB2C08
				.countCtrl
					position absolute
					right 20upx
					bottom 30upx
					text
						display inline-block
						width 60upx
						height 50upx
						text-align center
						line-height 50upx
						border 1upx solid #EEEEEE
						&:nth-child(2)
							border none
							border-top 1upx solid #EEEEEE
							border-bottom 1upx solid #EEEEEE
		.cartFooter
			position fixed
			display flex	
			bottom 0rpx
			height 96upx
			line-height 96upx
			width 100%
			background #fff
			font-size 30upx
			.iconfont
				font-size 40upx
				margin 0 20upx
				color: #999
				&.selected
					color: #BB2C08
			.right 
				height 96upx
				position absolute
				right 0
				.totalPrice
					display inline-block
					height 96upx
					line-height 96upx
				.preOrder
					display inline-block
					background #DD1A21
					width 225upx
					height 96upx
					line-height 96upx
					text-align center
					font-size 28upx
					color #fff
					margin-left 30upx
		.hint
			text-align center
			font-size 28upx
					
</style>
